.btn {
  display: block;
  position: relative;
  width: 170px;
  padding: 11px 0;
  margin: 30px 0;
  background: #000;
  color: #fff;
  text-align: center;
  overflow: hidden;
  outline: none;
}
.btn:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: -4.5em;
  height: 100%;
  width: 3em;
  transform: skewX(-45deg) translateX(0);
  transition: none;
  background-color: rgba(255, 255, 255, 0.5);
}
.btn:hover {
  color: #fff;
  box-shadow: none;
}
.btn:hover:after {
  transform: skewX(-45deg) translateX(22em);
  transition: all .5s linear;
}
